import * as React from 'react';
import PropTypes from 'prop-types';
import kebabCase from 'lodash/kebabCase';
import { Link, graphql } from 'gatsby';
import Layout from '../components/layout';
import * as style from '../styles/style.module.css';

const TagList = ({
  data: {
    allMdx: { group },
  },
}) => (
  <Layout
    pageType="tag"
    pageTitle="タグリスト"
    pageDescription="月の森デザインの Web研究室「ツキノラボ」で使用しているタグを、一覧表示しているページです。"
  >
    <ul className={style.tagList}>
      {group.map(tag => (
        <li key={tag.fieldValue} className={style.tagList__item}>
          <Link to={`/tag-list/${kebabCase(tag.fieldValue)}/`} className={style.tagList__link}>
            {tag.fieldValue} ({tag.totalCount})
          </Link>
        </li>
      ))}
    </ul>
  </Layout>
);

TagList.propTypes = {
  data: PropTypes.shape({
    allMdx: PropTypes.shape({
      group: PropTypes.arrayOf(
        PropTypes.shape({
          fieldValue: PropTypes.string.isRequired,
          totalCount: PropTypes.number.isRequired,
        }).isRequired
      ),
    }),
  }),
};

export default TagList;

export const pageQuery = graphql`
  query {
    allMdx(limit: 2000) {
      group(field: frontmatter___tags) {
        fieldValue
        totalCount
      }
    }
  }
`;
